import React,{ useState } from "react"
import ItemKresek from "./ItemKresek"
import './Kresek.css'
const Kresek = (props) => {
    const ambilIdKresek = (objectAddToCart) => {
        props.getIdBarang(objectAddToCart)
    }
    return(
    <div  className="ItemKresek" >
        {props.dataKresek.map(barangKresek =>(
            <ItemKresek className="Items"
            idBarang={ambilIdKresek}
            idBarangKresek = {barangKresek.id}
            nama = {barangKresek.nama}
            harga = {barangKresek.harga}
            stok = {barangKresek.stok}
            />
        ))}
    </div>
    )
}

export default Kresek