import './ItemKresek.css'
import PlastikKresek from '../img/PlastikKresek.jpg'
import Cart from '../img/Cart.png'
import React from 'react'



const ItemKresek = (props) => {
    const addToCart = (event) => {
        props.idBarang(event.target.value)
    }
    return(
        <div id="Item-Kresek">
            <img className="GambarProduk"src={PlastikKresek}/>
                <h5>
                    
                    <span>{props.nama}</span><br/>
                    <span><button onClick={addToCart} value={props.idBarangKresek} id="TombolBeli" src={Cart}>{props.harga} ðŸ›’</button></span><br/>
                    <span id="stok">stok {props.stok}</span>
                </h5>
        </div>
    )
}

export default ItemKresek