import PlastikKresek from '../img/PlastikKresek.jpg';
import './Belanjaan.css'

const Belanjaan = (props) => {
    const jumlah = props.jumlahBelanjaan
    return(
    <div className="wrapper-cart">
        {props.cartBelanjaan.map((barangBelanjaan,index) => ( 
            <div id="Item-Belanjaan">
            <img className="GambarProdukBelanjaan"src={PlastikKresek}/>
            <h5>
                
                <span>{barangBelanjaan.nama}</span><br/>
                <span id="hargaBelanjaan">{barangBelanjaan.harga}</span><br/>
                <span>{jumlah[index]}</span>
            </h5>
            </div>
        ))}
    </div>
    )
}

export default Belanjaan